@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.mainBlock {
  min-height: 70vh;
  background-color: $bgColor;

  .main {
    .greeting {
      text-transform: uppercase;
      margin-top: 100px;
      text-align: center;

      h1 {
        font-size: 2em;
        letter-spacing: 10px;
        margin-bottom: 20px;
        margin-top: 20px;
      }

      .hi {
        display: inline-block;
        border-bottom: 1px solid $primaryColor;
        padding: 10px;
        letter-spacing: 4px;
        margin-bottom: 10px;
        font-size: 1.5em;
      }

      .profession {
        letter-spacing: 4px;
        margin-bottom: 10px;
        font-size: 1.5em;
        font-weight: 700;
        animation: type 7s steps(27) infinite;
        overflow: hidden;
        white-space: nowrap;
        //border-right: 1px solid $primaryColor;
        /* @include keyframes(type) {
           0% {
             width: 0ch;
           }
           50% {
             width: 27ch;
           }
           100% {
             width: 27ch;
           }
         }*/
      }
    }

    .photo {
      width: 350px;
      height: 350px;
      margin-top: 100px;
      margin-bottom: 50px;
      background-size: cover;
      background-repeat: no-repeat;
      align-self: center;
      transition: 0.5s;
      border: 1px solid $primaryColor;
      &:hover{
        transform: scale(1.1,1.1);
      }
    }

    @media screen and (max-width: 1189px) {
      justify-content: center;
    }
  }
}

